<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap mb32">
                <div class="h1">Статистика проекта</div>
            </div>
            <div class="main-title__right">
                <a @click="update" href="javascript:void(0);" class="link-icon proj-statistic-update">
                    <span class="i i-refresh" :class="{start: startUpdate}"></span>
                    <span class="text">Обновить</span>
                </a>
            </div>
        </div>
        <div class="proj-statistic" v-if="incomes.length > 0">
            <div class="proj-statistic-item income">
                <div class="ttl flex-row">
                    <div class="text-size-xxl text-bold l">Доход</div>
