                        </div>
                        <div class="sub-row">
                            <span class="icon-arrow-1"></span>
                            <span class="icon-arrow-2"></span>
                            <span class="icon-arrow-3"></span>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">1 купон</div>
                                <div class="count">{{number_format(total.users.countCoupon1)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.users.percent.countCoupon1)}}%</div>
                            </div>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">2 купон</div>
                                <div class="count">{{number_format(total.users.countCoupon2)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.users.percent.countCoupon2)}}%</div>
                            </div>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">3+ купон</div>
                                <div class="count">{{number_format(total.users.countCoupon3)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.users.percent.countCoupon3)}}%</div>
                            </div>
                        </div>
                        <div class="bells">
                          <tooltip
                                  :direction="'right-center-direction'"
                                  :add-class-wrap="''"
                                  :isBtnClose="false"
                                  :add-style="{width: 249 + 'px'}">
                              <span slot="tooltipBtn">
                                <div class="bell-check">

                                    <span class="i i-bell-check mr4"></span>
                                    <span class="text-size-xs text-color-grey">?</span>
                                </div>
                              </span>

                              <div slot="tooltipContent">
                                  Чтобы узнать количество пользователей, включивших уведомления, нужно написать Андрею, так как это легче посмотреть вручную в FireBase, чем кодить, так как у них нет нужного API
                              </div>
                          </tooltip>

                        </div>
                    </div>
                </div>

                <div class="col">
                    <div class="proj-statistic-item">
                        <div class="ttl flex-row mb24">
                            <div class="text-size-l text-bold l">Поставщики</div>
                            <div class="text-size-l text-bold r">{{number_format(total.providers.total)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">
                                <div class="icn">
                                    <span>В процессе</span>
                                    <tooltip
                                            :direction="'right-center-direction'"
                                            :add-class-wrap="''"
                                            :isBtnClose="false"
                                            :add-style="{width: 298 + 'px'}">
                                        <span slot="tooltipBtn">
                                          <span class="i i-info4"></span>
                                        </span>

                                        <div slot="tooltipContent">
                                            <div class="flex-row mb16">
                                                <div class="text-size-m l">Новые</div>
                                                <div class="text-size-m r">{{(total.providers.percent.new)}}%</div>
                                                <div class="text-size-m text-bold r">{{number_format(total.providers.new)}}</div>
                                            </div>
                                            <div class="flex-row mb16">
                                                <div class="text-size-m l">На проверке</div>
                                                <div class="text-size-m r">{{(total.providers.percent.mod)}}%</div>
                                                <div class="text-size-m text-bold r">{{number_format(total.providers.mod)}}</div>
                                            </div>
                                            <div class="flex-row mb16">
                                                <div class="text-size-m l">Ждут договор</div>
                                                <div class="text-size-m r">{{(total.providers.percent.countCoupon3)}}%</div>
                                                <div class="text-size-m text-bold r">{{number_format(total.providers.countCoupon1)}}</div>
                                            </div>
                                            <div class="flex-row ">
                                                <div class="text-size-m l">Ждём документы</div>
                                                <div class="text-size-m r">{{(total.providers.percent.wait)}}%</div>
                                                <div class="text-size-m text-bold r">{{number_format(total.providers.wait)}}</div>
                                            </div>
                                        </div>
                                    </tooltip>
                                </div>

                            </div>
                            <div class="text-size-m r">{{(total.providers.percent.process)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.providers.process)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Отклонены</div>
                            <div class="text-size-m r">{{(total.providers.percent.refuse)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.providers.refuse)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Заблокированные</div>
                            <div class="text-size-m r">{{(total.providers.percent.ban)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.providers.ban)}}</div>
                        </div>
                        <div class="flex-row">
                            <div class="text-size-m l">Одобрены</div>
                            <div class="text-size-m r">{{(total.providers.percent.verify)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.providers.verify)}}</div>
                        </div>
                        <div class="sub-row">
                            <span class="icon-arrow-1"></span>
                            <span class="icon-arrow-3"></span>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">Активные</div>
                                <div class="count">{{number_format(total.providers.active)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.providers.percent.active)}}%</div>
                            </div>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">Пассивные</div>
                                <div class="count">{{number_format(total.providers.passive)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.providers.percent.passive)}}%</div>
                            </div>
                        </div>
                        <div class="sub-row">
                            <span class="icon-arrow-1"></span>
                            <span class="icon-arrow-2"></span>
                            <span class="icon-arrow-3"></span>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">1 акция</div>
                                <div class="count">{{number_format(total.providers.actionCount1)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.providers.percent.actionCount1)}}%</div>
                            </div>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">2 акции</div>
                                <div class="count">{{number_format(total.providers.actionCount2)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.providers.percent.actionCount2)}}%</div>
                            </div>
                            <div class="item">
                                <div class="text-size-xs text-color-grey">3+ акции</div>
                                <div class="count">{{number_format(total.providers.actionCount3)}}</div>
                                <div class="text-size-xs text-color-grey">{{(total.providers.percent.actionCount3)}}%</div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col">
                    <div class="proj-statistic-item">
                        <div class="ttl flex-row mb24">
                            <div class="text-size-l text-bold l">Акции</div>
                            <div class="text-size-l text-bold r">{{number_format(total.actions.total)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Всероссийские</div>
                            <div class="text-size-m r">{{(total.actions.percent.all)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.actions.all)}}</div>
                        </div>
                        <div class="flex-row">
                            <div class="text-size-m l">Местячковые</div>
                            <div class="text-size-m r">{{(total.actions.percent.point)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.actions.point)}}</div>
                        </div>
                    </div>

                    <div class="proj-statistic-item">
                        <div class="ttl flex-row mb24">
                            <div class="text-size-l text-bold l">Купоны</div>
                            <div class="text-size-l text-bold r">{{number_format(total.coupons.total)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Новые</div>
                            <div class="text-size-m r">{{(total.coupons.percent.new)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.coupons.new)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">В работе</div>
                            <div class="text-size-m r">{{(total.coupons.percent.work)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.coupons.work)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Отложенные</div>
                            <div class="text-size-m r">{{(total.coupons.percent.timeout)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.coupons.timeout)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Аннулированные постав.</div>
                            <div class="text-size-m r">{{(total.coupons.percent.annulProvider)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.coupons.annulProvider)}}</div>
                        </div>
                        <div class="flex-row mb16">
                            <div class="text-size-m l">Аннулированные нами</div>
                            <div class="text-size-m r">{{(total.coupons.percent.annul)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.coupons.annul)}}</div>
                        </div>
                        <div class="flex-row">
                            <div class="text-size-m l">Погашенные</div>
                            <div class="text-size-m r">{{(total.coupons.percent.repay)}}%</div>
                            <div class="text-size-m text-bold r">{{number_format(total.coupons.repay)}}</div>
                        </div>

                        <!-- График Pie -->
                        <div class="pie">
                          <!-- Первый элемент массива - это мобайл -->
                          <pie-chart :values="[total.coupons.mobile,total.coupons.desktop]"></pie-chart>
                          <div class="percents">
                            <div class="mobile">
                              <div class="text-bold text-size-m mb8"> {{total.coupons.percent.mobile}}% </div>
                            </div>
                            <div class="desktop">
                              <div class="text-bold text-size-m"> {{total.coupons.percent.desktop}}% </div>
                            </div>
                          </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import LineChart from '@/components/LineChart.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import PieChart from '@/components/PieChart.vue';
    import {number_format} from '@/helpers/formatted.js';

    export default {
        metaInfo() {
            return {
                title: 'Статистика проекта',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "ProjectStatistic",
        components: {
            LineChart,
            Tooltip,
            PieChart,
        },
        data: function () {
            return {
                interval: false
            }
        },
        computed: {
            ...mapState({
                graph: state => state.moderatorStatistic.graph,
                maxProfit: state => state.moderatorStatistic.maxProfit,
                months: state => state.moderatorStatistic.months,
                total: state => state.moderatorStatistic.total,
                lastMonth: state => state.moderatorStatistic.lastMonth,
                startUpdate: state => state.moderatorStatistic.startUpdate,
            }),
            incomes() {
                let incomes = [];
                for (let itemIndex in this.graph) {
                    let item = this.graph[itemIndex];
                    incomes.push({
                        value: item.value.profit,
                        tooltip: {
                            formatter: this.getFormatter(item.month, item.value.orders, item.value.providers, item.value.actions, item.value.coupons, item.value.coupons_ext),
                        }
                    });
                }
                return incomes;
            }
        },
        mounted() {
            this.$store.dispatch('moderatorStatistic/getItems');
            this.$store.dispatch('moderatorStatistic/checkUpdate').then((e)=>{
                if(e){
                    this.$store.commit('moderatorStatistic/setStartUpdate',true);
                    this.interval =  setInterval(()=>{
                        this.$store.dispatch('moderatorStatistic/checkUpdate').then((e)=>{
                            if(!e){
                                clearInterval(this.interval);
                                this.$store.dispatch('moderatorStatistic/getItems');
                            }
                        });
                    },5000);
                }
            });
        },
        methods: {
            //Доход выводится выводится самостояталельно, просто берётся из data графика
            getFormatter(month, apps, supps, coupons, buy, redeem) {
                return `<div class="tooltip right-center-direction" style="width: 144px;">
                        <div class="tooltip__content">
                          <div class="tooltip-ttl">` + month + `</div>
                          <div class="flex-row text-bold"><div class="l">Доход</div> <div class="r"> {c} Р</div></div>
                          <div class="flex-row"><div class="l">Заявок</div>      <div class="r">` + apps + `</div></div>
                          <div class="flex-row"><div class="l">Поставщиков</div> <div class="r">` + supps + `</div></div>
                          <div class="flex-row"><div class="l">Акций</div>       <div class="r">` + coupons + `</div></div>
                          <div class="flex-row"><div class="l">Оформлено</div>   <div class="r">` + buy + `</div></div>
                          <div class="flex-row"><div class="l">Погашено</div>   <div class="r">` + redeem + `</div></div>
                      </div>
                    </div>`
            },
            update() {
                if(!this.startUpdate){
                    this.$store.dispatch('moderatorStatistic/postStartUpdate');
                    this.interval =  setInterval(()=>{
                        this.$store.dispatch('moderatorStatistic/checkUpdate').then((e)=>{
                            if(!e){
                                clearInterval(this.interval);
                                this.$store.dispatch('moderatorStatistic/getItems');
                            }
                        });
                    },5000);
                }
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
        },
        watch: {},
    }
</script>
<style scoped>
    .i-refresh.start {
        animation: 1s linear 0s normal none infinite running rot;
        -webkit-animation: 1s linear 0s normal none infinite running rot;
    }
    @keyframes rot {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
    @-webkit-keyframes rot {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
</style>
